import uniIcons from '../uni-icons/uni-icons.vue'
export default {
	name: 'UniIndexedList',
	components: {
		uniIcons
	},
	props: {
		loaded: {
			type: Boolean,
			default: false
		},
		idx: {
			type: Number,
			default: 0
		},
		list: {
			type: Object,
			default () {
				return {}
			}
		},
		showSelect: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		onClick(idx, index) {
			this.$emit("itemClick", {
				idx,
				index
			})
		}
	}
}
